$font-style: normal;
$font-variant: normal;
$font-weight: normal;
$font-size: 1rem;
$line-height: 1.5;

@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600;700;900&display=swap");
$font-family: Nunito, sans-serif;

$color-gray: #7a7a7a;
$color-gray2: #c0c0c0;

$color-white: #fff;
$color-black: #000;

// Define tipografia
html {
  font: $font-style $font-variant $font-weight #{$font-size}/#{$line-height}
    $font-family;
}

footer {
  background: $color-black;
  .footer {
    color: $color-white;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
    max-width: 1248px;
    margin: 0 auto;
    padding: 25px 0;

    .address {
      display: flex;
      flex-direction: column;
      gap: 15px;
      h3 {
        font-style: normal;
        font-weight: bold;
        font-size: 20px;
        line-height: 27px;
        &::after {
          content: "";
          border-bottom: 5px solid #ffffff;
          width: 44px;
          display: block;
          margin: 15px 0 20px 0;
        }
      }
      p,
      a {
        font-style: normal;
        font-weight: normal;
        font-size: 13px;
        line-height: 12px;
      }
    }

    .contact {
      ul {
        display: flex;
        flex-direction: column;
        gap: 25px;

        li {
          color: $color-black;
          font-style: normal;
          font-weight: 600;
          font-size: 12px;
          line-height: 16px;
          width: 195px;
          height: 38px;
          border-radius: 5px;
          background: $color-white;
          a {
            display: flex;
            align-items: center;
            justify-content: space-evenly;
            height: 100%;

            p {
              max-width: 127px;
            }
          }
        }
      }
    }

    .created {
      display: flex;
      gap: 25px;
      align-items: center;
      font-style: normal;
      font-weight: normal;
      font-size: 10.6017px;
      line-height: 14px;
    }
  }
  .base-footer {
    background: $color-gray2;
    height: 10px;
  }
}

button {
  background: transparent;
  border: none;
  outline: none;
}

html,
body,
h3,
p {
  margin: 0;
  padding: 0;
  font-family: $font-family;
}

input {
  outline: none;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

li {
  list-style: none;
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80px;
  width: 100%;
  max-width: 1248px;
  margin: 0 auto;

  .menu {
    font-family: $font-family;
    &__search {
      width: 100%;
      max-width: 718px;
      margin: 0 41px 0 121px;
      position: relative;
      display: flex;
      .boxsearch {
        font-style: normal;
        font-weight: normal;
        font-size: 13px;
        line-height: 18px;
        color: $color-gray;
        width: 100%;
        border: none;
        border-bottom: 1px solid $color-gray;
        padding-bottom: 10px;
      }
      .btn-search {
        position: absolute;
        right: 0px;
      }
    }

    &__user-cart {
      width: 155px;
      height: 20px;
      display: inline-flex;
      justify-content: space-between;
      font-style: normal;
      font-weight: normal;
      font-size: 13px;
      line-height: 18px;
      color: $color-gray;

      .count {
        background: #f8475f;
        border-radius: 50%;
        font-style: normal;
        font-weight: bold;
        font-size: 10px;
        line-height: 14px;
        color: $color-white;
        padding: 1px 5px;
      }

      > div {
        display: flex;
        align-items: center;
        justify-content: space-between;
        white-space: nowrap;
        > img {
          margin-right: 5px;
        }
      }
    }
  }
}

main {
  .banner {
    @media screen and (max-width: 768px) {
      &__desktop {
        display: none;
      }
    }
    @media screen and (min-width: 768px) {
      &__mobile {
        display: none;
      }
    }
  }
}
